/* src/components/OptionsList.css */
.options-list {
    width: 15vw;
    background-color: #f8f9fa;
    padding: 1.5vh;
    border-right: 0.05vw solid #dee2e6;
    box-sizing: border-box;
}

.options-list h3 {
    margin-top: 0;
    margin-bottom: 1vh;
    font-size: 1vw;
}

.option-button {
    margin-bottom: 0.5vh;
}

.option-button button {
    display: block;
    width: 100%;
    padding: 0.5vh;
    border: 0.05vw solid #ccc;
    border-radius: 0.2vw;
    background-color: white;
    cursor: pointer;
    text-align: left;
    font-size: 0.625vw;
    transition: background-color 0.2s;
}

.option-button button:hover {
    background-color: #e9ecef;
}